<template>
  <div>
         <div class="guess-box">
              <div class="guess-left">
                  <receiveList 
                  @initSuperior="initSuperior"
                  :receiveFigure1="receiveFigure1"
                  :receiveFigure2="receiveFigure2"
                  :receiveFigure3="receiveFigure3"
                  ></receiveList>
               </div>
              <div class="guess-right">
                  <incomeList></incomeList>
              </div>
         </div>
  </div>
</template>
<script>
import { BN } from "@/common/util";
import  receiveList  from "../views/guessPlay/receive.vue" 
import  incomeList  from "../views/guessPlay/incomeList" 

 export default {
   components:{
      incomeList,
      receiveList,
   },
  data() {
    return {
       receiveFigure1:"",
       receiveFigure2:"",
       receiveFigure3:"",
    };
  },
  mounted() {
    this.$nextTick(()=>{
       this.initQuery(0);
       this.initQuery(1);
       this.initQuery(2);
    })
  },
  methods: {
     initSuperior(){
        this.initQuery(0);
        this.initQuery(1);
        this.initQuery(2);
     },
       // 展示领取显示数字
    initQuery(index){
      this.$api.pendingTokenFn(index).then((res)=>{
           if(index == 1){
               this.receiveFigure2 = new BN(res).toDisplay(18).toFixed(2);
           }else if(index == 2){
               this.receiveFigure3 = new BN(res).toDisplay(18).toFixed(2);
           }else{
              this.receiveFigure1 = new BN(res).toDisplay(18).toFixed(2);
           }
      })
    },
  },
 };
</script>
<style lang="less" scoped>
 .guess-box{
   display: flex;
   flex-wrap: wrap;
   width: 100%;
   height: 700px;
   padding: 50px;
   .guess-left{
     flex: 1;
    //border: 1px solid  red;
   }
   .guess-right{
     flex: 2;
   }
 }
     @media screen and (max-width: 479px) {
      .guess-box{
            padding: 0 !important;
       }
  }
      
</style>