</template>
<script>
import { BN } from "@/common/util";
import {mapState,mapGetters} from "vuex";
export default {
  data() {
    return {
      dayAmount1: "",
      amount1: "",
      dayReward1: "",
      reward1: "",

      dayAmount2: "",
      amount2: "",
      dayReward2: "",
      reward2: "",

      dayAmount3: "",
      amount3: "",
      dayReward3: "",
      reward3: "",
    };
  },
  computed: {
    ...mapState('app',['address']),
    ...mapGetters(['language']),
  },
  mounted() {
    this.initQuery(0);
    this.initQuery(1);
    this.initQuery(2);
  },
  methods: {
    initQuery(index) {
      let time = parseInt(new Date().getTime() / (24 * 60 * 60 * 1000));
      this.$api.getUserInfoFn(index, time).then((res) => {
       
        if(index == 1){
           this.dayAmount2 = new BN(res.dayAmount).toDisplay(18).toFixed(1);
          this.amount2 = new BN(res.amount).toDisplay(18).toFixed(1);
          this.dayReward2 = new BN(res.dayReward).toDisplay(18).toFixed(1);
          this.reward2 = new BN(res.reward).toDisplay(18).toFixed(1);
        }else if(index == 2){
          this.dayAmount3 = new BN(res.dayAmount).toDisplay(18).toFixed(1);
          this.amount3 = new BN(res.amount).toDisplay(18).toFixed(1);
          this.dayReward3 = new BN(res.dayReward).toDisplay(18).toFixed(1);
          this.reward3 = new BN(res.reward).toDisplay(18).toFixed(1);
        }else{
          this.dayAmount1 = new BN(res.dayAmount).toDisplay(18).toFixed(1);
          this.amount1 = new BN(res.amount).toDisplay(18).toFixed(1);
          this.dayReward1 = new BN(res.dayReward).toDisplay(18).toFixed(1);
          this.reward1 = new BN(res.reward).toDisplay(18).toFixed(1);
        }
      });
    },
  },
};
</script>
